html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.playground {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 40%;
  background-color: #1e1e2c;
}

.code-editor {
  height: 33.33%;
  overflow: hidden;
  position: relative;
}

.editor-header {
  height: 30px;
  content: attr(title);
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-size: 18px;
  color: #fafafa;
}

.react-codemirror2 {
  max-height: calc(100% - 30px);
  overflow: auto;
}

.result1 {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 40%;
  overflow: hidden;
}

.iframe {
  width: 100%;
  height: 100%;
}

.buttons {
  position: absolute;
  border-radius: 5px;
  width: 50px;
  right: 0%;
  color: aliceblue;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}
